body {
  margin: 0;
}

.app {
  padding: 20px 40px;
  height: calc(100vh - 40px);
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

textarea {
  display: block;
  background-color: var(--primary-background-color) !important;
  border: 1px solid #c3c6d4;
  border-color: var(--ui-border-color);
  border-radius: 4px;
  box-sizing: border-box;
  color: #323338;
  color: var(--primary-text-color);
  font-family: Roboto, Rubik, Noto Kufi Arabic, Noto Sans JP, sans-serif;
  font-family: var(--font-family);
  font: 400 14px/24px Roboto, Rubik, Noto Kufi Arabic, Noto Sans JP, sans-serif;
  font: var(--font-general-label);
  height: 100%;
  outline: 0;
  padding: 8px 16px;
  min-height: 88px;
  transition: border-color .1s ease-in;
  transition: border-color var(--motion-productive-medium) ease-in;
  resize: none;
  width: 100%;
}

textarea:hover {
  border-color: #000;
  border-color: var(--color-black);
}

textarea:focus {
  border-color: #0073ea;
  border-color: var(--primary-color);
}

label {
  color: var(--primary-text-color);
  display: block;
  margin-bottom: 4px;
  font-size: 14px;
  font-weight: 700;
}

input {
  background-color: var(--primary-background-color) !important;
}

.has-margin-bottom {
  margin-bottom: 24px;
}

.has-white-background {
  background-color: var(--primary-background-color) !important;
}

.has-icon-success svg {
  color: var(--color-success);
}

.large-fieldset {
  flex: 1 1 50%;
}

.medium-fieldset {
  flex: 1 1 25%;
}

.small-fieldset {
  flex: 1 1 12.5%;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
}

.toast {
  width: 390px;
  z-index: 999;
}

.searchbox {
  max-width: 490px;
  min-width: 295px;
  width: 50%;
}

/* Monday Style Overrides */
.virtualized-list--wrapper {
  border-radius: 4px;
  box-shadow: var(--box-shadow-small);
  box-sizing: border-box;
  overflow-x: hidden !important;
  position: absolute !important;
  top: 100% !important;
  width: 100% !important;
  z-index: 99 !important;
}

.is-hidden-combobox .virtualized-list--wrapper {
  display: none;
}

.combobox-option {
  box-sizing: border-box;
  border-radius: 0 !important;
}

.combobox--wrapper-list {
  padding: 0 !important;
}

.combobox--wrapper--custom {
  position: relative;
}

.custom--wrapper--input {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}

.custom--wrapper--input.is-active {
  display: block;
}

.custom-input-component {
  -webkit-font-smoothing: antialiased;
  -webkit-font-smoothing: var(--font-smoothing-webkit);
  -moz-osx-font-smoothing: grayscale;
  -moz-osx-font-smoothing: var(--font-smoothing-moz);
  border-radius: 4px;
  border: 1px solid var(--ui-border-color);
  box-sizing: border-box;
  color: var(--primary-text-color);
  font: var(--font-general-label);
  line-height: 22px;
  height: 100%;
  outline: 0;
  overflow: hidden;
  padding: 8px 16px;
  position: relative;
  text-overflow: ellipsis;
  transition: border-color .1s ease-in;
  transition: border-color var(--motion-productive-medium) ease-in;
  white-space: nowrap;
  width: 100%;
}

.custom-input-icon--wrapper {
  align-items: center;
  -webkit-appearance: none;
  border-radius: 4px;
  display: flex;
  height: 24px;
  justify-content: center;
  pointer-events: none;
  position: absolute;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
}

.custom-input-icon--wrapper svg {
  width: 16px;
  height: 16px;
  pointer-events: all;
  transform: rotate(0) scale(1);
  color: var(--icon-color);
  font-size: 14px;
  transition: color var(--motion-productive-short) var(--motion-timing-enter), transform var(--motion-productive-short) var(--motion-timing-enter);
  will-change: transform;
}

.custom-input-icon--wrapper:hover {
  background-color: var(--primary-background-hover-color);
  cursor: pointer;
}

.chips--wrapper.is-active div {
  color: var(--text-color-on-primary);
}

.custom-chip-component {
  display: flex !important;
  height: auto !important;
  justify-content: center;
  box-sizing: border-box;
}

.custom-chip-component:not(:last-child) {
  margin-bottom: 4px;
}

.date-picker-component {
  position: absolute;
  z-index: 100;
  border: 1px solid var(--ui-border-color);
  border-radius: 4px;
}

.date-picker-component div {
  border-radius: 4px;
}

.list-item-avatar {
  margin-right: 8px;
}

.list-title {
  font-size: 14px !important;
  font-weight: 700 !important;
  padding: 0 !important;
}

.monday-style-list--dense {
  height: 100%;
}

.list-item--xxs {
  padding: 4px 8px !important;
}